bo, que consiste en seis cuadrados conectados que, cuando se pliegan, forman el cubo. El texto explica que el patronaje es la representación en 2D (bidimensional) de un volumen en 3D (tridimensional) mediante la elaboración de planos, moldes o patrones de prendas de vestir para el cuerpo."}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-6"},[_c('img',{staticClass:"mb-5",attrs:{"src":require("@/assets/curso/temas/t1/estructura.svg"),"alt":"Imagen describe la relación entre la estructura del cuerpo humano, su representación en planos y los moldes utilizados en la confección de prendas. De izquierda a derecha, se observan figuras estilizadas de un hombre y una mujer etiquetadas como \"Estructura\". A la derecha de estas figuras, se presenta una representación en \"Plano\" que descompone las formas tridimensionales en una vista bidimensional. Finalmente, se ven los \"Moldes\", que son las plantillas específicas para cortar las telas y confeccionar las prendas de vestir, detallando las formas de las piezas necesarias para cubrir la estructura del cuerpo.","data-aos":"fade-down"}})]),_c('div',{staticClass:"col-lg-6"},[_c('img',{staticClass:"mb-5",attrs:{"src":require("@/assets/curso/temas/t1/estructura1.svg"),"alt":"La imagen describe el proceso de creación de prendas de vestir a partir de la estructura del cuerpo humano. De izquierda a derecha, se ven dos figuras humanas estilizadas etiquetadas como \"Figura 3D\" o \"Estructura\". Luego, estas figuras se traducen en un \"Plano 2D\", que descompone la forma tridimensional en una vista bidimensional. A continuación, se presentan los \"Moldes\", que son las plantillas para cortar las piezas de tela necesarias. Finalmente, el proceso culmina en la \"Prenda\", que es el producto final confeccionado a partir de los moldes, ejemplificado con un saco. Esto ilustra cómo la representación tridimensional del